@import "../../scss/variables";

.section-header {
  color: $text1;
  display: flex;
  align-items: center;
  padding-bottom: 5px;
  margin-bottom: 10px;
  border-bottom: 1px solid $separatorLine;
  justify-content: space-between;
  flex-wrap: wrap;

  .title {
    display: flex;
    align-items: center;
    
    i {
      color: $iconDefault;
      font-size: 20px;
      margin-right: 10px;
    }

    h3 {
      margin: 0;
      font-size: 16px;
      line-height: 16px;
      color: $text1;
    }
  }

  .actions {
    display: flex;
    align-items: center;

    i {
      color: $iconDefault;
      font-size: 20px;
      margin-right: 10px;
    }

    h3 {
      margin: 0;
      font-size: 16px;
      line-height: 16px;
      color: $text1;
    }
  }
}
