@import '../../../../../scss/variables';

.note {
    background: #ffffff;
    border-radius: 5px;
    padding-top: 2px;
    padding-bottom: 1px;
    margin: 1rem 0;

    &__date-time {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;

        &__day {
            font-size: 28px;
        }
    }

    &__file-list {
        overflow: hidden;
        word-break: break-word;
        justify-content: center;
        margin-bottom: 1rem;
    }

    .custom {
        &__icon {
            &__show-detail {
                color: $info;
                cursor: pointer;
            }

            &__delete {
                color: red;
                cursor: pointer;
            }
        }
    }
}