@import "../../../../scss/variables";

$paddingTop: 30px;

.booking-modal {

  .modal-content {
    overflow: hidden;
  }

  .close {
    float: none;
    opacity: 1;

    span {
      color: $altText1;
      font-size: 30px;
    }
  }

  .btn-group {
    .btn-primary {
      background-color: $inputBg;
      color: $text1;
      border-color: transparent !important;

      .fa.fa-circle {
        transform: translateX(-10px);
        font-size: 20px;
        color: $radioGroup;
      }

      &:not(:disabled):not(.disabled).active {
        background-color: $inputBgH;

        .fa.fa-circle {
          color: $altText1;

          &:before {
            content: "\f058";
          }
        }
      }
    }
  }

  .select-user {
    .select2-selection {
      padding: 0 10px;
      height: calc(3rem + 2px);
      display: flex;
      align-items: center;
    }
  }

  .select2-user {
    // transform: translate(-5px, -5px);
  }

  .preview {
    background-color: $text1;
    color: $altText1;
    padding: 20px;
    font-size: 14px;

    .label-highlight {
      text-transform: uppercase;
      color: $accentText1;
    }

    .color-light {
      opacity: 0.6;
    }

    .time {
      position: relative;
      padding: $paddingTop 0 0 15px;

      &:before {
        content: "";
        width: 4px;
        height: calc(100% - #{$paddingTop});
        background: #fff;
        position: absolute;
        border-radius: 10px;
        left: 0;
      }
    }

    .duration {
      .value {
        line-height: 1;
        font-size: 22px;
      }

      .label {
        font-size: 12px;
        font-weight: 100;
      }
    }

    .space {
      img {
        width: 100%;
        max-height: 300px;
        border: 2px solid $altText1;
        border-radius: 10px;
      }
    }

    .money {
      font-size: 22px;

      .symbol {
        font-weight: lighter;
      }

      .value {
        margin-left: 3px;
      }
    }
  }

  .detail {
    position: relative;

    .booking-from {
      padding: 30px;
    }

    .form-control-label {
      text-transform: capitalize;
    }

    .header {
      background-color: #eff2f2;
      color: $text1;
      padding: 25px 10px;
      margin: 0 -15px;
      display: flex;
      align-items: center;

      i {
        color: $iconDefault;
        font-size: 22px;
        margin-right: 10px;
      }

      h2 {
        margin: 0;
        font-size: 22px;
        font-weight: bold;
        color: $text1;

        &.inactivate {
          opacity: 0.2;
        }
      }
    }

    .title-note {
      font-size: 12px;
      margin-left: 5px;
      color: $text3;
    }

    .appointment {
      padding-top: 5px;
      padding-bottom: 5px;

      &:first-child {
        padding-top: 30px;
      }
    }
  }
}

.appointment {
  &.section-collapse {
    .info,
    .service,
    .date-time {
      display: none;
    }
  }
}

body.room-unavailable {
  cursor: not-allowed !important;
}