.provider-message {
  overflow: hidden;
  margin: 10px 0;

  .msg-box {
    max-width: 75%;
  }

  .time_date {
    color: #747474;
    display: block;
    font-size: 10px;
    margin: 8px 0 0;
    font-style: italic;
  }

  .message-content {
    background: #ebebeb none repeat scroll 0 0;
    border-radius: 15px;
    color: #646464;
    font-size: 14px;
    margin: 0;
    padding: 5px 10px 5px 12px;
    width: 100%;
  }

  .notification {
    color: #646464;
    font-size: 12px;
  }

  p {
    margin-bottom: 0px;
  }

  .file-list {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    justify-content: center;
    margin-bottom: 1rem;
  }

  .file-name {
    font-size: 12px;
    font-style: italic;
    margin: 2px 0px;
  }
}
