@import '../../../../../scss/variables';

.billing {
    .form-control {
        text-align: center;
    }

    .w-175 {
        width: 175px;
    }

    .date-time {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        max-width: 40px;

        .date {
            font-size: 28px;
        }
    }

    .badge-custom {
        border: 1px solid $orange;
    }
}

.payment {
    background-color: $gray-300;

    .table th,
    .table td {
        border: none;
    }
}